
h1 {
  font-weight: normal;
  font-family: 'roboto-black';
  font-size: 25px;
  color: #76AFBC;
  font-weight: 800;
}
h2{
font-size: 18px;
color: #76AFBC;
font-family: 'roboto-regular';
}
.middle-text{
  text-align: center;
    width: 60%;
        margin: auto;
    display: block;
}
p {
  color: #76AFBC;
  font-family: 'roboto-regular';
  font-size: 16px;
  text-align: justify;
  text-justify: inter-word;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.outside{
  width: 30%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.icon{
    width: 25%;
    height: 70px;
    background-color: #537d86;
     padding-top: 25px;
       color: #FFF;
       font-size: 20px;
}
.text{
      width: 75%;
    align-text: left;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    background-color: #76AFBC;
    padding-top: 25px;
      color: #FFF;
      font-weight: bold;
}
@media screen and (max-width: 1023px) {
.outside{
    width: 75% !important;
}
.text{
      padding-top: 10px !important;
}
}



